
.skin-1 .title {
  background: rgba(0, 0, 0, 0.3);
}
.skin-2 .icon-item {
  margin: 0 auto !important;
  float: none !important;
}
.skin-2 .title {
  background: rgba(0, 0, 0, 0.3);
}
.skin-4 .title {
  background-image: -webkit-gradient(linear, left bottom, left top, from(rgba(0, 0, 0, 0.6)), to(rgba(0, 0, 0, 0.3)));
  background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.3) 100%);
}
.skin-7 .icons-list-wrap,
.skin-8 .icons-list-wrap,
.skin-6 .icons-list-wrap,
.skin-5 .icons-list-wrap {
  overflow-y: hidden !important;
}
.skin-7 .icons-list,
.skin-8 .icons-list,
.skin-6 .icons-list,
.skin-5 .icons-list {
  float: left;
}
.skin-8 {
  background: transparent !important;
}

/* reset and golab style*/
html, body {
  height: 100%;
  margin: 0;
}
ul, li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
select:focus {
  outline: none;
}
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
#app {
  height: 100%;
}

/* element ui reset */
.el-icon-circle-check.icon-circle-check-custom::before {
  content: '';
  display: inline-block;
  background-image: url(assets/images/icons/check-circle-o.png);
  background-size: 28px 28px;
  width: 28px;
  height: 28px;
}
.selected .icon-circle-check-custom::before {
  background-image: url(assets/images/icons/check-circle-o-selected.png);
  background-size: 28px 28px;
}
.el-icon-error.icon-error-custom:before {
  content: '';
  display: inline-block;
  width: 16px;
  height: 16px;
  background: url(assets/images/icons/icon-delete2.png) center center no-repeat;
  background-size: 16px 16px;
}
.el-dialog__header {
  padding: 13px 20px;
  font-size: 14px;
  background: #424242;
  color: #fff;
}
.el-dialog__header .el-dialog__title {
    font-size: 14px;
    line-height: 1;
    color: #fff;
}
.el-dialog__body {
  padding: 20px;
}
.el-dialog__headerbtn {
  top: 14px;
}
.el-message-box {
  border: none;
}
.el-message-box .el-message-box__header {
    padding: 13px 20px;
    background: #272727;
}
.el-message-box .el-message-box__header .el-message-box__title {
      font-size: 14px;
      color: #fff;
}
.el-message-box .el-message-box__header .el-message-box__headerbtn {
      top: 13px;
}
.el-message-box .el-message-box__content {
    margin-top: 30px;
    color: #222;
}
.el-message-box .el-message-box__content .el-message-box__message {
      padding-left: 50px;
}
.el-message-box .el-icon-error {
    font-size: 40px !important;
    color: red;
}
.el-message-box .el-icon-warning {
    font-size: 40px !important;
    color: red;
}
.el-message-box .el-icon-success {
    font-size: 40px !important;
}
.el-message-box .el-message-box__btns {
    margin: 10px 0 5px;
    text-align: center;
}
.el-message-box .el-message-box__btns button {
      width: 150px;
      height: 40px;
      font-size: 14px;
}



